import { useContext } from "react";
import FavoritesContext from "../store/favorites-context";
import MeetupList from "../components/meetups/MeetupList";

function FavoritesPage() {
    const favoriteCtx = useContext(FavoritesContext);
    let content; let favoritesList; let parsedJSON;

    /*if (favoriteCtx.totalFavorites === 0) {
        parsedJSON = JSON.parse(localStorage.getItem("favorites"));
        if (parsedJSON !== null) {
            favoritesList = parsedJSON;
        }
        else favoritesList = favoriteCtx.favorites;
    }
    else favoritesList = favoriteCtx.favorites;*/

    favoritesList = favoriteCtx.favorites;

    if (favoriteCtx.totalFavorites === 0 && parsedJSON === null)
        content = <p>You have no favorites</p>
    else
        content = <MeetupList meetups={favoritesList} />

    return <section>
        <h1>My Favorites</h1>
        {content}
    </section>
}

export default FavoritesPage;